.container {
    padding: 0 7px;
}
.row {
    display: flex;
    flex-wrap: wrap;
    @include fluid-type(margin-left margin-right, 320px, 1920px, -7px, -20px);

    [class^="col-"] {
        @include fluid-type(padding-left padding-right, 320px, 1920px, 7px, 20px);
    }

    &--small {
        @include fluid-type(margin-left margin-right, 320px, 1920px, -5px, -12px);

        [class^="col-"] {
            @include fluid-type(padding-left padding-right, 320px, 1920px, 5px, 12px);
        }
    }

    &--ai-center {align-items: center;}

    &--jc-center {justify-content: center;}
    &--jc-space-between {justify-content: space-between;}
}
[class^="col-"] {
    flex: 0 0 auto;
}
.col-xs-1 {
    width: 8.333333%;
}
.col-xs-2 {
    width: 16.666666%;
}
.col-xs-3 {
    width: 25%;
}
.col-xs-4 {
    width: 33.333333%;
}
.col-xs-5 {
    width: 41.666666%;
}
.col-xs-6 {
    width: 50%;
}
.col-xs-7 {
    width: 58.333333%;
}
.col-xs-8 {
    width: 66.666666%;
}
.col-xs-9 {
    width: 75%;
}
.col-xs-10 {
    width: 83.333333%;
}
.col-xs-11 {
    width: 91.666666%;
}
.col-xs-12 {
    width: 100%;
}

