html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-size: calc(14px + 4 * (100vw - 320px) / 1600);
  line-height: 1.5;
  font-weight: 400;
  font-family: Arial, Helvetica, sans-serif;
  color: #364364;
  background-color: #eceef3;
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}
*:focus {
  outline: 0;
}
*:before, *:after {
