.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  border-radius: 3px;
  transition: 0.3s;
  font-weight: 700;
  line-height: 1;
  border: 0;
}
.btn:disabled {
  opacity: 0.5;
  cursor: default;
}
.btn--default {
  height: calc(28px + 8 * (100vw - 320px) / 1600);
  padding-left: calc(14px + 6 * (100vw - 320px) / 1600);
  padding-right: calc(14px + 6 * (100vw - 320px) / 1600);
  font-size: calc(13px + 3 * (100vw - 320px) / 1600);
}
.btn--default-square, .btn--default-square-xs {
  height: calc(28px + 8 * (100vw - 320px) / 1600);
  width: calc(28px + 8 * (100vw - 320px) / 1600);
